.stripe {
  width: 100%;
  height: 100%;
  position: relative;
}

.stripe__inner {
  width: 100%;
  height: 100%;
  z-index: 1;
  background: repeating-linear-gradient(45deg, #ffa5ba 25%, #ffa5ba 50%, #ffb8ca 50%, #ffb8ca 75%) 0 0 / 100px 100px;
  animation: stripeBackgroundPosition 3s linear infinite;
  position: relative;
}

.stripe:before {
  content: "";
  width: calc(100% + 100px);
  height: 100%;
  background: repeating-linear-gradient(45deg, #ffb8ca 25%, #ffb8ca 50%, #ffa5ba 50%, #ffa5ba 75%) 0 0 / 100px 100px;
  animation: stripeTransform 3s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.stripe:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
}

@keyframes stripeTransform {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-100px);
  }
}

@keyframes stripeBackgroundPosition {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: -100px 0;
  }
}

